import { Size, GridSize } from "app/models/Types";
import { Constants } from "app/config";
import { observable, action, computed } from "mobx";
import ImageModel from "./ImageModel";

type Subscriber = {
  uid: string;
  count: number;
};

export default class UIModel {

  @observable checked: ImageModel;
  @action.bound setChecked = (img: ImageModel) => {
    if (this.checked && img !== this.checked) this.checked.checked = false;
    this.checked = img.checked ? img : undefined;
  };

  @observable basket: ImageModel[] = [];
  @computed get totalBasketKs(): number {
    return this.basket.reduce((t, c) => t + c.inBasket, 0);
  }
  @computed get totalBasketPrice(): number {
    return this.basket.reduce(
      (t, c) => t + c.inBasket * Number(c.prices[0]),
      0
    );
  }
  @action.bound resetBasket = async () => {
    for (let i: number = this.basket.length - 1; i >= 0; i--) {
      this.basket[i].inBasket = 0;
      this.updateBasket(this.basket[i]);
    }
  };
  @action.bound updateBasket = (newItem: ImageModel) => {
    const index = this.basket.findIndex(o => o.id === newItem.id);
    if (index > -1) {
      if (!(newItem.inBasket > 0)) {
        this.basket[index].inBasket = 0;
        this.basket.splice(index, 1);
      }
    } else this.basket.push(newItem);
  };

  private _loadingSubscribers: { [uid: string]: Subscriber } = {};
  @observable isLoading: boolean = false;
  @action setLoading = (uid: string, loading: boolean) => {
    const subscriber = this._loadingSubscribers[uid];
    if (subscriber) {
      if (loading) {
        this._loadingSubscribers[uid].count++;
      } else {
        this._loadingSubscribers[uid].count--;
        if (this._loadingSubscribers[uid].count === 0) {
          delete this._loadingSubscribers[uid];
        }
      }
    } else {
      if (loading) {
        this._loadingSubscribers[uid] = { uid: uid, count: 1 };
      }
    }
    this.isLoading = Object.keys(this._loadingSubscribers).length > 0;
  };
  itemSize: Size = new Size(Constants.CARD_SIZE, Constants.CARD_SIZE);
  pageSize: Size = new Size();
  gridSize: GridSize = new GridSize();
  itemsCount: number = 0;
  currentItem: number = 0;
  countPages: () => number = () =>
    Math.ceil(this.itemsCount / this.gridSize.getCount());
  currentPage: () => number = () =>
    Math.ceil(this.currentItem / this.gridSize.getCount());

  setGridSize = (gridSize?: GridSize) => {
    if (gridSize && !this.gridSize.isEqual(gridSize)) {
      this.gridSize.setBy(gridSize);
      this.setPageSize(
        new Size(
          Constants.CARD_SIZE * this.gridSize.columns,
          Constants.CARD_SIZE * this.gridSize.rows
        )
      );
    }
  };
  setPageSize = (pageSize?: Size) => {
    if (pageSize && !this.pageSize.isEqual(pageSize)) {
      this.pageSize.setBy(pageSize);
      this.setGridSize(
        new GridSize(
          Math.floor(this.pageSize.width / this.itemSize.width),
          Math.floor(this.pageSize.height / this.itemSize.height)
        )
      );
    }
  };
  constructor(pageSize?: Size, gridSize?: GridSize) {
    switch (true) {
      case pageSize !== undefined:
        this.setPageSize(pageSize);
        break;
      case gridSize !== undefined:
        this.setGridSize(gridSize);
        break;
      default:
        // throw('You need to specify at least one argument!');
        this.setPageSize(new Size());
    }
  }
}
