import React, { Component } from "react";
import * as S from "./styles";
import { observer, inject } from "mobx-react";
import { Tabs, Spin, Modal, Badge, Statistic } from "antd";
import UIModel from "app/models/UIModel";
import ImageModel from "app/models/ImageModel";
import { DeleteTwoTone, CloseCircleTwoTone } from "@ant-design/icons";
import DetailView from "../common/DetailView";
import miniCart from "app/assets/images/mini-cart.png";

const TabPane = Tabs.TabPane;

type Props = {
  ui?: UIModel;
};
type State = { modal: boolean; id: string };

@inject("ui")
@observer
export default class Basket extends Component<Props, State> {
  state: State = { modal: false, id: undefined };
  private handleReset = async (): Promise<void> => {
    await this.props.ui.resetBasket();
  };
  private handleClick = (id: string) => () => {
    const basketItem: ImageModel = this.props.ui.basket.find(o => o.id === id);
    if (basketItem) {
      this.setState({ modal: true, id: basketItem.id });
    }
  };
  private handleClose = () => {
    this.setState({ modal: false, id: undefined });
  };

  render() {
    return (
      <S.Container>
        <S.Icon>
          <img alt="mini-cart.png" src={miniCart} />
        </S.Icon>
        <S.MyTabs
          // onChange={key => this.handleClick(key)()}
          type="card"
        >
          {this.props.ui.basket.map((basketItem: ImageModel) => {
            return (
              <TabPane
                tab={
                  <S.BadgeWrapper
                    id={basketItem.id}
                    onClick={
                      !this.state.modal
                        ? this.handleClick(basketItem.id)
                        : undefined
                    }
                  >
                    <S.Img
                      alt={"Polozka-" + basketItem.id}
                      src={basketItem.url}
                    />
                    <div className="badgeWrapper">
                      <Badge
                        count={basketItem.inBasket}
                        title={`${basketItem.inBasket} ks`}
                        style={{ background: "#52c41a" }}
                      />
                    </div>

                    <Modal
                      visible={this.state.modal}
                      width="482px"
                      footer={null}
                      onCancel={this.handleClose}
                      closeIcon={
                        <CloseCircleTwoTone
                          style={{ fontSize: "25px" }}
                        />
                      }
                    >
                      <DetailView
                        img={basketItem}
                        inBasket={basketItem.inBasket}
                        updateBasket={this.props.ui.updateBasket}
                        onHide={this.handleClose}
                      />
                    </Modal>
                  </S.BadgeWrapper>
                }
                key={basketItem.id}
              />
            );
          })}
        </S.MyTabs>
        <S.BadgeWrapper>
          <S.ResetBtn
            disabled={this.props.ui.basket.length === 0}
            onClick={this.handleReset}
            icon={
              <DeleteTwoTone
                twoToneColor={
                  this.props.ui.basket.length === 0 ? "#babdbe" : "#546e7a"
                }
                style={{ fontSize: "30px" }}
              ></DeleteTwoTone>
            }
          ></S.ResetBtn>
          {this.props.ui.basket.length > 0 && (
            <div
              className="badgeWrapper"
              style={{ right: "20px", top: "-5px" }}
            >
              <Badge
                count={this.props.ui.totalBasketKs}
                title={`Celkom ${this.props.ui.totalBasketKs} ks`}
                style={{ cursor: "default" }}
              />
            </div>
          )}
        </S.BadgeWrapper>
        <S.SaveBtn
          disabled={this.props.ui.basket.length === 0}
          style={{
            width: "125px",
            height: "66px",
            margin: 0,
            marginTop: "-3px",
            borderRadius: "0px",
            background: this.props.ui.basket.length ? "#819ca9" : ""
          }}
        >
          <Statistic
            title="Potvrdiť nákup:"
            style={{
              fontWeight: this.props.ui.basket.length ? "bold" : "normal"
            }}
            value={this.props.ui.totalBasketPrice}
            precision={2}
            suffix="€"
            valueStyle={{ color: "#29434e" }}
          ></Statistic>
        </S.SaveBtn>
      </S.Container>
    );
  }
}
