import React, { Component } from "react";
import * as S from "./styles";
import { Descriptions, Badge, Drawer } from "antd";
import ImageModel from "app/models/ImageModel";
import UIModel from "app/models/UIModel";
import { observer, inject } from "mobx-react";
import ReactCardFlip from "react-card-flip";
import * as I from "@ant-design/icons";
import { CatalogItem } from "app/api";
import BasketForm from "app/components/common/BasketForm";
import miniCart from "app/assets/images/mini-cart.png";

type Props = {
  store: ImageModel;
  ui?: UIModel;
};

type State = {
  isFlipped: boolean;
  isHovered: boolean[];
  drawer: boolean;
};

@inject("ui")
@observer
export class Image extends React.Component<Props> {
  state: State = { isFlipped: true, isHovered: [false, false], drawer: false };
  handlers = {
    enterHovered: e =>
      this.setState({
        isHovered: this.state.isHovered.map((c, i) =>
          Number(e.currentTarget.id.split("_")[1]) === i ? true : c
        )
      }),
    leaveHovered: e =>
      this.setState({
        isHovered: this.state.isHovered.map((c, i) =>
          Number(e.currentTarget.id.split("_")[1]) === i ? false : c
        )
      }),
    switchFlip: () => this.setState({ isFlipped: !this.state.isFlipped }),
    switchChecked: () => {
      this.props.store.checked = !this.props.store.checked;
      this.props.ui.setChecked(this.props.store);
    },
    loaded: () => {
      this.setState({ isFlipped: false });
    },
    onSwitchDrawer: () => this.setState({ drawer: !this.state.drawer })
  };
  actions = () => [
    <div
      id="act_0"
      onMouseOver={this.handlers.enterHovered}
      onMouseOut={this.handlers.leaveHovered}
      onClick={this.handlers.switchChecked}
    >
      <I.CheckCircleTwoTone
        style={{ fontSize: "41px" }}
        twoToneColor={getValueByComb(
          [this.props.store.checked, this.state.isHovered[0]],
          ["lightgrey", "darkgrey", "darkgrey", "lightgrey"]
        )}
      />
    </div>,
    <div
      id="act_1"
      onMouseOver={this.handlers.enterHovered}
      onMouseOut={this.handlers.leaveHovered}
      onClick={this.handlers.switchFlip}
    >
      {!this.state.isFlipped ? (
        <I.ProfileTwoTone
          twoToneColor={this.state.isHovered[1] ? "darkgrey" : "lightgrey"}
          style={{ fontSize: "41px" }}
        />
      ) : (
        <I.PictureTwoTone
          twoToneColor={this.state.isHovered[1] ? "darkgrey" : "lightgrey"}
          style={{ fontSize: "41px", transform: "scale(0.9,1.1)" }}
        />
      )}
    </div>
  ];
  public render() {
    const {
      store: { id, url, inBasket, checked, item, discount }
    } = this.props;
    return (
      <S.CardWrapper
        onDoubleClick={
          this.state.drawer ? undefined : this.handlers.switchChecked
        }
        style={this.state.drawer ? { overflow: "hidden" } : {}}
      >
        {this.state.drawer && (
          <Drawer
            title="Nákupný košík"
            placement="bottom"
            mask={true}
            maskClosable={true}
            onClose={this.handlers.onSwitchDrawer}
            visible={this.state.drawer}
            getContainer={false}
            style={{ position: "absolute" }}
            destroyOnClose={true}
            headerStyle={{ background: "grey" }}
            bodyStyle={{ background: "lightgrey", padding: "10px" }}
          >
            <BasketForm
              img={this.props.store}
              inBasket={inBasket}
              updateBasket={this.props.ui.updateBasket}
              onHide={this.handlers.onSwitchDrawer}
            />
          </Drawer>
        )}
        <ReactCardFlip
          isFlipped={this.state.isFlipped}
          flipDirection="horizontal"
          infinite={false}
        >
          <S.Image
            key="front"
            cover={
              <S.Cover>
                <img
                  alt={`Produkt Id = ${id}`}
                  src={url}
                  onLoad={this.handlers.loaded}
                />
                {inBasket > 0 && (
                  <BadgeWrapper
                    inBasket={inBasket}
                    onClick={this.handlers.onSwitchDrawer}
                  />
                )}
                {discount < 0 && (
                  <div
                    style={{
                      position: "absolute",
                      right: "8px",
                      top: "135px",
                      cursor: "pointer"
                    }}
                    id="act_1"
                    onMouseOver={this.handlers.enterHovered}
                    onMouseOut={this.handlers.leaveHovered}
                    onClick={this.handlers.switchFlip}
                  >
                    <S.DiscountSticker>
                      <div className="discount-sticker__inner">
                        {`${discount}%`}
                      </div>
                    </S.DiscountSticker>
                  </div>
                )}
              </S.Cover>
            }
            actions={this.actions()}
            className={checked ? "checked" : "#"}
            style={{
              background: checked ? "lightgrey" : "#ffffff"
            }}
          ></S.Image>
          <S.Image
            key="back"
            cover={
              <S.Cover>
                {inBasket > 0 && (
                  <BadgeWrapper
                    inBasket={inBasket}
                    onClick={this.handlers.onSwitchDrawer}
                  />
                )}
                <ItemDescription item={item} />
              </S.Cover>
            }
            actions={this.actions()}
            className={checked ? "checked" : "#"}
            style={{
              background: checked ? "lightgrey" : "#ffffff"
            }}
          />
        </ReactCardFlip>
      </S.CardWrapper>
    );
  }
}

const ItemDescription: React.FC<{ item: CatalogItem }> = ({ item }) => {
  return (
    <Descriptions
      style={{
        height: "184px",
        width: "184px",
        background: "#ffffff",
        padding: "5px",
        textAlign: "left"
      }}
      title={item.variant.look.product.instance.title}
      column={1}
    >
      <Descriptions.Item label="Cena">
        <span style={{ fontWeight: "bold" }}>{getPrices(item)[0]}€</span>
        {" ("}
        <span style={{ textDecoration: "line-through" }}>
          {getPrices(item)[1]}€
        </span>
        )
      </Descriptions.Item>
      <Descriptions.Item label="Skladom">
        {item.instance.availability === "out of stock"
          ? "Nie"
          : item.instance.availability === "in stock"
          ? "Áno"
          : "?"}
      </Descriptions.Item>
      <Descriptions.Item label="Kód">{item.code}</Descriptions.Item>
    </Descriptions>
  );
};

function BadgeWrapper(props: { inBasket: number; onClick: () => void }) {
  return (
    <S.BadgeWrapper onClick={props.onClick}>
      <img
        alt="mini-cart.png"
        src={miniCart}
        style={{ height: "50px", width: "50px" }}
      />
      {/* <MiniCartSvg size={50} color="red" /> */}
      <div className="badgeWrapper">
        <Badge
          count={props.inBasket}
          style={{ background: "#52c41a" }}
          title={`${props.inBasket} ks`}
        />
      </div>
    </S.BadgeWrapper>
  );
}

function getPrices(item: CatalogItem): string[] {
  return [
    Number(item.instance.sale_price.split(" ")[0]).toFixed(2),
    Number(item.variant.instance.price.split(" ")[0]).toFixed(2)
  ];
}

function getValueByComb(combs: boolean[], vals: any[]): any {
  const i: number = combs.reduce(
    (sum, b, i) => sum + (b ? Math.pow(2, i) : 0),
    0
  );
  return vals[i];
}

