import styled from "styled-components";
import { Tabs } from "antd";
import { TabsProps } from "antd/lib/tabs";

export const Collections = styled(Tabs)<TabsProps>`
  background-color: #f6f6f6;
  // flex: 1;
  // display: flex;
  // flex-direction: column;
  align-items: start; // center;
  width: 100%;

  &.ant-tabs {
    padding: 26px 26px 0px 26px;
  }

  & .ant-tabs-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  & .ant-tabs-tabpane-active {
    flex: 1 !important;
  }

  & .ant-tabs-bar {
    border: 0;
    margin: 0px;
  }

  &.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-nav-container {
    height: 45px;
  }

  &.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab {
    height: 45px;
    margin-right: 10px;
    padding: 0 6px;
    background-color: #ffffff;
    border: 1px solid rgba(151, 151, 151, 0.15);
    border-radius: 5px;
    box-shadow: -1px 4px 9px 0 rgba(240, 242, 244, 0.58);
  }
  &.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab:hover {
    border: 1px solid #c6c6c6;
  }
  &.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab-active {
    border: 1px solid #c6c6c6;
  }

  & .ant-card-bordered {
    border: 0;
  }

  & .ant-tabs-tab-active .ant-card-meta-title {
    // font-weight: 600;
    text-shadow: 0 0 0.01px;
    opacity: 1;
  }

  & .ant-tabs-tab-active .ant-avatar {
    opacity: 1;
  }
`;
