import React, { useState } from "react";
import { observer, inject } from "mobx-react";
import UIModel from "app/models/UIModel";
import { Spin } from "antd";
import styled from "styled-components";
import logo from "app/assets/images/logo.png";

type Props = {
  ui?: UIModel;
};
const Logo: React.FC<Props> = inject("ui")(
  observer(props => {
    const { ui } = props;
    return (
      <StyledLogo>
        <img src={logo} alt="logo" />
        {ui.isLoading && (
          <Spin style={{ position: "absolute", left: "225px", top: "15px" }} />
        )}
      </StyledLogo>
    );
  })
);

export default Logo;

const StyledLogo = styled.div`
  flex: 0 0 47px;
  position: relative;

  & img {
    height: 47px;
    width: 329px;
    box-shadow: -1 0px 20px 0px #f0f2f4;  
  }
`;