export function randId(low: number, high: number, ids?: string[]) {
  if (!ids) ids = [];
  let id: number = low + Math.floor(Math.random() * (high - low));
  while (ids.some(item => item === id.toString())) {
    id = low + Math.floor(Math.random() * (high - low));
  }
  return id.toString();
}

const isObject = (obj: any) =>
  typeof obj === "object" && obj !== null && !Array.isArray(obj);
const isFunction = (func: any) => typeof func === "function";
export function isGeneratorLike(obj: any) {
  return isObject(obj) && isFunction(obj.next) && isFunction(obj.throw);
}

export class IdArray {}
