import React, { Component } from "react";
import ImageModel from "app/models/ImageModel";
import { Layout, Row, Descriptions, Button, Card } from "antd";
import BasketForm from "./BasketForm";
import { url } from "inspector";
import styled from "styled-components";

type Props = {
  img: ImageModel;
  inBasket: number;
  updateBasket: (img: ImageModel) => void;
  onHide?: () => void;
};
const DetailView: React.FC<Props> = props => {
  const { img, inBasket, updateBasket, onHide } = props;
  const { id, url, item } = img;
  return (
    <Layout>
      <Layout.Header
        style={{ backgroundColor: "transparent", padding: "10px" }}
      >
        <BasketForm
          img={img}
          inBasket={inBasket}
          updateBasket={updateBasket}
          onHide={onHide}
          style={{ marginTop: "10px" }}
        />
      </Layout.Header>
      <Layout>
        <Row>
          <Image
            cover={
              <Cover>
                <Descriptions
                  style={{
                    width: "184px",
                    background: "#ffffff",
                    padding: "5px",
                    textAlign: "left"
                  }}
                  title={item.variant.look.product.instance.title}
                  column={1}
                >
                  <Descriptions.Item label="Cena">
                    <span style={{ fontWeight: "bold" }}>
                      {img.prices[0]}€
                    </span>
                    {" ("}
                    <span style={{ textDecoration: "line-through" }}>
                      {img.prices[1]}€
                    </span>
                    )
                  </Descriptions.Item>
                  <Descriptions.Item label="Skladom">
                    {item.instance.availability === "out of stock"
                      ? "Nie"
                      : item.instance.availability === "in stock"
                      ? "Áno"
                      : "?"}
                  </Descriptions.Item>
                  <Descriptions.Item label="Kód">{item.code}</Descriptions.Item>
                </Descriptions>
              </Cover>
            }
            // actions={this.actions()}
          ></Image>
          <Image
            cover={
              <Cover>
                <img alt={`Produkt Id = ${id}`} src={url} />
              </Cover>
            }
            // actions={this.actions()}
          ></Image>
        </Row>
        <Layout.Footer>
          <Button type="primary" style={{ width: "100%" }}>
            Prejsť na kartu
          </Button>
        </Layout.Footer>
      </Layout>
    </Layout>
  );
};

export default DetailView;

// styles
const Cover = styled.div`
  position: relative;
  height: 100%;
  width: 100%;
  & img {
    // height: 184px;
    width: 184px;
  }
`;

const ANIMATION_LENGTH = "0.1s";
const ANIMATION_DELAY = "0.1s";

const Image = styled(Card)`
  &.ant-card {
    margin: 8px;
    padding: 8px;
    border-radius: 5px;
    background-color: #ffffff;
    border: solid 1px transparent;
    box-shadow: 0 2px 75px 0 transparent;
    transition: all ${ANIMATION_LENGTH} linear ${ANIMATION_DELAY};
    perspective: 1000px;
    z-index: 1;
  }

  &.ant-card:last-child {
    margin-left: -30px;
    width: 240px;
    z-index: 0;
  }

  &.ant-card .ant-card-cover {
    transform-style: preserve-3d;
  }

  & > .ant-card-cover {
    // height: 184px;
    width: 100%;
  }

  & > .ant-card-body {
    display: none;
  }
`;
