import * as api from "app/api";
import { observable, observe, action } from "mobx";
import CollectionModel from "app/models/CollectionModel";
import UIModel from "app/models/UIModel";
import { Size } from "app/models/Types";

export default class Store {
  // private _loadedImagesEvent; // observed event for loading images while infinite scrolling
  @observable ui: UIModel;
  @observable collections: { [id: string]: CollectionModel } = {};
  @observable currentCollection: CollectionModel;
  @action setCurrentCollection = id => {
    this.currentCollection = this.collections[id];
    this.ui.checked = this.currentCollection.images.find(img => img.checked);
    this.ui.itemsCount = this.currentCollection.images.length;
    /*
    this._loadedImagesEvent = observe(this.currentCollection.images, () => {
      this.ui.itemsCount = this.currentCollection.images.length;
    });
    */
  };

  constructor(pageSize?: Size) {
    this.ui = new UIModel(pageSize);
    api
      .loadCollections()
      .then(data => {
        this.collections = data;
        if (Object.keys(this.collections).length > 0)
          this.setCurrentCollection(Object.keys(this.collections)[0]);
      })
      .catch(error => Promise.reject({ ...error }));
  }
}
