import React, { Component } from "react";
import * as S from "./styles";
import Collection from "app/components/Collection";
import { Images } from "app/components/Images";
import { Tabs } from "antd";
import Store from "app/models/Store";
import { observer } from "mobx-react";

const TabPane = Tabs.TabPane;
type Props = { store: Store; loaded: () => void };
type State = { loaded: boolean };

@observer
export default class Collections extends Component<Props, State> {
  state: State = { loaded: false };
  componentDidUpdate() {
    if (!this.state.loaded) {
      this.props.loaded();
      this.setState({ loaded: true });
    }
  }

  public render() {
    return (
      <S.Collections
        onChange={key => {
          this.props.store.setCurrentCollection(key);
        }}
        type="card"
        // animated={true} // {{ inkBar: true, tabPane: true }}
        // tabPosition="left"
        // rc-tabs
        // navWrapper={c => (<div>{c}</div>)}
        // prevIcon={<div>aaa</div>}
      >
        {Object.keys(this.props.store.collections).map(id => {
          const val = this.props.store.collections[id];
          const loadPromise: (count: number) => Promise<void> = async (
            count: number
          ) => {
            await val.loadImages(count);
          };
          return (
            <TabPane
              style={{ position: "relative" }}
              tab={<Collection store={val} />}
              key={val.id}
            >              
              <Images images={val.images} load={loadPromise}></Images>
            </TabPane>
          );
        })}
      </S.Collections>
    );
  }
}
