type InstyleResponse<T = any> = {
  links: {
    next: string;
    prev: string;
    limit: number;
    offset: number;
  };
  results: T[];
};

type CatalogColor = number[];

export type CatalogItem = {
  id: string;
  code: string;
  instance: {
    condition: string;
    availability: string;
    sale_price: string;
  };
  variant: {
    id: string;
    code: string;
    instance: {
      size: number;
      price: string;
    };
    look: {
      id: string;
      code: string;
      instance: {
        color: string;
        "palette-colors": {
          indices: number[];
          colors: CatalogColor[];
          weights: number[];
        };
        "dominant-colors": {
          colors: CatalogColor[];
          weights: number[];
        };
      };
      product: {
        id: string;
        code: string;
        instance: {
          title: string;
          description: string;
          link: string;
          google_product_category: any;
          gender: any;
          adult: "yes" | "no";
          custom_labels: string[];
          pattern: any;
        };
        brand: {
          id: string;
          code: string;
          instance: {
            name: string;
          };
        };
        product_type: {
          id: string;
          code: string;
          instance: {};
        };
      };
      primary_image: {
        id: string;
        code: string;
        instance: {
          url: string;
          uuid: string;
        };
      };
    };
  };
};

export async function loadCollections(): Promise<{
  [id: string]: CollectionModel;
}> {
  try {
    const response: AxiosResponse<InstyleResponse> = await axios.get(
      Constants.API_URL + "categories"
      /*
      {
        headers: {
          "x-api-key": Constants.API_KEY,
          Authorization: "None",
          Accept: "application/json"
        }
      }
      */
    );
    // const result: { [id: string]: CollectionModel } = {};
    const result = await Promise.all(
      response.data.results.map(async category => {
        const product: AxiosResponse<InstyleResponse<{
          product_id: string;
        }>> = await axios.get(
          Constants.API_URL +
            "categories/" +
            category.id +
            "/product-categories?limit=1"
        );
        const item: AxiosResponse<CatalogItem> = await axios.get(
          Constants.API_URL + "items/" + product.data.results[0].product_id
        );
        const model = new CollectionModel(
          category.id,
          category.code,
          item.data.variant.look.primary_image.instance.url
        );
        return [category.id, model];
      })
    );

    const fromEntries = arr =>
      Object.assign({}, ...Array.from(arr, ([k, v]) => ({ [k]: v })));
    const r = fromEntries(result);
    return r;
  } catch (error) {
    return Promise.reject({ ...error });
  }
}

export async function* loadImages(
  count: number = 100,
  startIndex: number = 0,
  category: string = ""
): AsyncIterableIterator<ImageModel> {
  const params = { offset: startIndex, limit: count };
  try {
    const products: AxiosResponse<InstyleResponse<{
      product_id: string;
    }>> = await axios.get(
      Constants.API_URL + "categories/" + category + "/product-categories",
      { params: params }
    );
    /*
    for (let [key, value] of Object.entries(products.data.results)) {
    }
    */
    for (let i = 0; i < products.data.results.length; i++) {
      const product: { product_id: string } = products.data.results[i];
      const item: AxiosResponse<CatalogItem> = await axios.get(
        Constants.API_URL + "items/" + product.product_id
      );
      yield new ImageModel(
        startIndex + i,
        product.product_id,
        item.data.variant.look.primary_image.code,
        item.data
      );
    }
  } catch (error) {
    return Promise.reject({ ...error });
  }
}

