import styled from "styled-components";
import { Card } from "antd";
import shapeStcker from "app/assets/images/shape_sticker.svg";

const ANIMATION_LENGTH = "0.1s";
const ANIMATION_DELAY = "0.1s";
const CARD_ANIMATION_LENGTH = "1s";

export const DiscountSticker = styled.div`
  width: 70px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  // background: url(../../images/shape_sticker.svg) no-repeat 50% 50%;
  background: url(${shapeStcker}) no-repeat 50% 50%;

  & > div.discount-sticker__inner {
    font-family: 'Gotham Rounded',sans-serif;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    transform: rotate(14deg);
    width: 50px;
    margin-right: 5px;
    margin-bottom: 4px;
  }
`;

export const BadgeWrapper = styled.div`
  @keyframes display2 {
    from {
      transform: scale(0.1);
    }
  }
  animation: display2 0.25s;

  position: absolute;
  width: 50px;
  height: 50px;
  right: -4px;
  top: -4px;
  cursor: pointer;

  & div.badgeWrapper {
    position: absolute;
    right: 10px;
    top: 2px;
  }

  & div.badgeWrapper span {
    vertical-align: unset;
  }
`;

export const CardWrapper = styled.div`
  @keyframes display {
    from {
      transform: scale(0.1);
    }
  }
  animation: display ${CARD_ANIMATION_LENGTH};

  position: relative;
  flex: 0 0 218px;
  height: 218px;

  &:hover {
    z-index: 2;
  }

  & .react-card-flip {
    height: 218px;
  }

  & div.ant-drawer-content-wrapper {
    height: 140px !important;
    width: 202px;
    margin-left: 8px;
    margin-top: 8px;
  }

  & div.ant-drawer-mask {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.15);
    // background-color: transparent;
  }

  & div.ant-drawer.ant-drawer-open .ant-drawer-mask {
    opacity: 1;
  }
`;

export const Cover = styled.div`
  position: relative;
  height: 100%;
  width: 100%;
  & img {
    height: 184px;
    width: 184px;
  }
`;

export const Image = styled(Card)`
  &.ant-card {
    margin: 8px;
    padding: 8px;
    border-radius: 5px;
    background-color: #ffffff;
    border: solid 1px transparent;
    box-shadow: 0 2px 75px 0 transparent;
    transition: all ${ANIMATION_LENGTH} linear ${ANIMATION_DELAY};
    perspective: 1000px;
  }
  &.ant-card: hover {
    margin: -12px -12px -49px -12px;
    padding: 28px;
    z-index: 1;
    border: solid 1px rgba(151, 151, 151, 0.15);
    box-shadow: 0 2px 75px 0 rgba(53, 46, 46, 0.57);
  }
  &.ant-card .ant-card-cover {
    transform-style: preserve-3d;
  }

  & > .ant-card-cover {
    height: 184px;
    width: 100%;
  }

  & > .ant-card-body {
    display: none;
  }

  & > .ant-card-actions {
    background: ${props =>
      props.className === "checked" ? "lightgrey" : "#ffffff"};
    border: 0;
    transition: height, background ${ANIMATION_LENGTH} linear ${ANIMATION_DELAY};
    height: 0;
    width: 100%;
    position: absolute;
    left: 0;
    position: absolute;
    overflow: hidden;
  }
  &:hover > .ant-card-actions {
    visibility: visible;
    opacity: 1;
    height: 66px;
    border: solid 1px rgba(151, 151, 151, 0.15);
    border-top: none;
    box-shadow: 0 2px 75px -75px rgba(53, 46, 46, 0.57);
  }

  & > .ant-card-actions > li {
    opacity: 0;
    transition: opacity 0.3s linear 0.2s;
  }
  &:hover > .ant-card-actions > li {
    opacity: 1;
  }

  & > .ant-card-actions > li,
  & > .ant-card-actions > li:not(:last-child) {
    border: 0;
  }

  & > .ant-card-actions > li > span {
    width: 41px;
    height: 41px;
    border-radius: 20px;
    background: transparent; // #f1f1f1;
  }
  & > .ant-card-actions > li:last-child > span {
    text-align: left;
    float: left;
  }
  & > .ant-card-actions > li:not(:last-child) > span {
    text-align: right;
    float: right;
  }
  & > .ant-card-actions > li > span {
    margin: 0 4px;
  }
  & > .ant-card-actions > li > span > .anticon {
    width: 100%;
    height: 100%;
    display: flex;
  }

  & > .ant-card-actions > li > span > .anticon > svg {
    margin: auto;
  }
  & > .ant-card-actions > li:last-child > span > .anticon > svg {
    position: relative;
    top: -2px;
  }
`;
