import React, { Component } from "react";
import { debounce, throtle } from "lodash";
import * as S from "./styles";
import { Image } from "app/components/Image";
import ImageModel from "app/models/ImageModel";
import UIModel from "app/models/UIModel";
import { Size } from "app/models/Types";
import { observer, inject } from "mobx-react";
import { Layout } from "antd";
import { Detail } from "../Detail";

type Props = {
  images: ImageModel[];
  ui?: UIModel;
  load?: (count: number) => Promise<void>;
};

@inject("ui")
@observer
export class Images extends Component<Props> {
  ref: React.RefObject<HTMLDivElement>;

  handleScroll = debounce(async () => {
    if (this.props.ui.isLoading) return;
    const aRef: HTMLDivElement = this.ref.current!;
    const ui = this.props.ui;
    ui.setPageSize(new Size(aRef.clientWidth, aRef.clientHeight));
    if (
      aRef.scrollHeight - aRef.scrollTop - aRef.clientHeight <=
      ui.itemSize.height
    ) {
      const count =
        ui.countPages() * ui.gridSize.getCount() -
        this.props.images.length +
        ui.gridSize.columns;
      if (count > 0) {
        this.props.ui.isLoading = true;
        await this.props.load(count);
        this.props.ui.itemsCount = this.props.images.length;
        this.props.ui.isLoading = false;
      }
    }
  }, 100);
  handleResize = debounce(async self => {
    if (this.props.ui.isLoading) return;
    const aRef = this.ref.current;
    const ui = this.props.ui;
    ui.setPageSize(new Size(aRef.clientWidth, aRef.clientHeight));
    const loadCount =
      (ui.countPages() + 1) * ui.gridSize.getCount() + ui.gridSize.columns - self.props.images.length;
    if (loadCount > 0) {
      this.props.ui.isLoading = true;
      await self.props.load(loadCount);
      this.props.ui.itemsCount = this.props.images.length;
      this.props.ui.isLoading = false;
    }
  }, 100);
  constructor(props) {
    super(props);
    this.ref = React.createRef();
  }

  async componentDidMount() {
    const aRef: HTMLDivElement = this.ref.current;
    this.ref.current.addEventListener("scroll", this.handleScroll);
    window.addEventListener("resize", this.handleResize(this));
  }
  componentWillUnmount() {
    this.ref.current.removeEventListener("scroll", this.handleScroll);
    window.removeEventListener("resize", this.handleResize);
  }

  public render() {
    const images = this.props.images;
    return (
      <Layout
        style={{
          height: "calc(100vh - 260px + 50px)",
          marginTop: "6px",
        }}
      >
        <S.Images ref={this.ref} style={{ paddingLeft: "12px" }}>
          {images.map(img => (
            <Image key={img.index} store={img} />
          ))}
        </S.Images>
        <Layout.Sider
          collapsible={true}
          breakpoint="sm"
          collapsedWidth="0"
          width="435px"
          // defaultCollapsed
          reverseArrow={true}
          theme="light"
          trigger={null}
          style={{ background: "transparent" }}
        >
          <Detail />
        </Layout.Sider>
      </Layout>
    );
  }
}
