import React, { Component } from "react";
import * as S from "./styles";
import Collections from "app/components/Collections";
import { Provider as MobXProvider } from "mobx-react";
import Store from "app/models/Store";
import Basket from "app/components/Basket";
import { Detail } from "app/components/Detail";
import Logo from "app/components/common/Logo";

import { observable } from "mobx";
import "./index.css";

type State = { isLoading: boolean; collapsed: boolean };
class FashionPredictale extends Component<{}, State> {
  state: State = { isLoading: true, collapsed: true };
  @observable store: Store = new Store();

  public render() {
    const { isLoading, collapsed } = this.state;
    return (
      <S.FashionPredictale>
        <MobXProvider ui={this.store.ui}>
          <Logo />
          <Collections
            store={this.store!}
            loaded={() => this.setState({ isLoading: false })}
          />
          {/* <S.SideDetail>
            <Detail />
          </S.SideDetail> */}
          {!isLoading && <Basket />}
        </MobXProvider>
      </S.FashionPredictale>
    );
  }
}

export default FashionPredictale;
