import React, { useState, useEffect, useCallback } from "react";
import { Form, InputNumber, Button } from "antd";
import ImageModel from "app/models/ImageModel";
import { DeleteTwoTone } from "@ant-design/icons";
import styled from "styled-components";

const FItem = styled(Form.Item)`
  margin-right: 0px !important;
`;

type Props = {
  img: ImageModel;
  inBasket: number; // used as trigger for outside changes of img.subprops
  updateBasket: (img: ImageModel) => void;
  onHide?: () => void; // for modals
  style?: React.CSSProperties;
};

const BasketForm: React.FC<Props> = props => {
  const { img, inBasket, updateBasket, onHide, style } = props;
  const [form] = Form.useForm();
  const [sameKs, setSameKs] = useState(false);

  /* useEffect(() => {
    form.setFieldsValue({ ks: img.inBasket ? img.inBasket : 1 });
    setSameKs(img.inBasket === form.getFieldValue("ks"));
  }, [img.inBasket]); */
  useEffect(() => {
    form.setFieldsValue({ ks: inBasket ? inBasket : 1 });
    setSameKs(inBasket === form.getFieldValue("ks"));
  }, [inBasket]);


  return (
    <Form
      style={style}
      form={form}
      layout="inline"
      onFinish={values => {
        img.inBasket = values["ks"];
        updateBasket(img);
        onHide ? onHide() : setSameKs(true);
      }}
      onReset={() => {
        img.inBasket = 0;
        updateBasket(img);
        onHide ? onHide() : setSameKs(false);
      }}
    >
      <FItem name="ks" label="Počet kusov">
        <InputNumber
          min={1}
          max={99}
          // onChange={v => setSameKs(img.inBasket === v)}
          onChange={v => setSameKs(inBasket === v)}
        />
      </FItem>
      <FItem name="submit">
        <Button
          disabled={sameKs}
          htmlType="submit"
          style={{
            background: sameKs ? "#babdbe" : "#546e7a",
            width: "135px",
            color: "#ffffff"
          }}
        >
          {/* {img.inBasket ? "Upraviť počet" : "Pridať do košíka"} */}
          {inBasket ? "Upraviť počet" : "Pridať do košíka"}
        </Button>
      </FItem>
      <FItem>
        <Button
          // disabled={!img.inBasket}
          disabled={!inBasket}
          htmlType="reset"
          icon={
            <DeleteTwoTone
              // twoToneColor={img.inBasket ? "#546e7a" : "#babdbe"}
              twoToneColor={inBasket ? "#546e7a" : "#babdbe"}
              style={{ fontSize: "25px", margin: "0px" }}
            />
          }
          style={{ marginLeft: "10px" }}
        />
      </FItem>
    </Form>
  );
};

export default BasketForm;
