import React, { Component } from "react";
import {
  Layout,
  Typography,
  Empty,
} from "antd";
import { observer, inject } from "mobx-react";
import UIModel from "app/models/UIModel";
import DetailView from "app/components/common/DetailView";

type Props = {
  ui?: UIModel;
};

@inject("ui")
@observer
export class Detail extends Component<Props> {
  public render() {
    const { checked, updateBasket } = this.props.ui;
    if (!checked) {
      return (
        <Layout>
          <Layout.Header style={{ backgroundColor: "transparent" }}>
            <Typography.Title level={4}>
              Vyberte produkt v ľavom zozname...
            </Typography.Title>
          </Layout.Header>
          <Empty description="" image={Empty.PRESENTED_IMAGE_DEFAULT} />
        </Layout>
      );
    }
    return (
      <DetailView img={checked} inBasket={checked.inBasket} updateBasket={updateBasket} />
    );
  }
}
