import styled from "styled-components";

export const FashionPredictale = styled.div`
  width: 100%;
  height: 100%;
  background-color: #f6f6f6;
  box-shadow: -1 4px 9px 0px rgba(240, 242, 244, 0.577);
  border: 1px solid rgb(198, 198, 198);
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const Logo = styled.div`
  flex: 0 0 47px;
  position: relative;

  & img {
    height: 47px;
    width: 329px;
    box-shadow: -1 0px 20px 0px #f0f2f4;  
  }
`;

