import ImageModel from "app/models/ImageModel";
import { observable } from "mobx";
import { loadImages } from "app/api";
import { isGeneratorLike } from "app/utils";
import UIModel from "app/models/UIModel";

export default class CollectionModel {
  id: string;
  title: string;
  icon: string;
  @observable images: ImageModel[];

  async loadImages(count: number, start?: number): Promise<void> {
    start = start === undefined ? this.images.length : start;
    const promise = loadImages(count, start, this.id);
    try {
      if (!isGeneratorLike(promise)) {
        return Promise.reject(
          new Error("Provided function must return a generator.")
        );
      }
      for await (let result of promise) {
        this.images.push(result);
      }
    } catch (e) {
      return Promise.reject(new Error(e));
    }
  }

  constructor(id: string, title: string, icon: string = "") {
    this.id = id;
    this.title = title;
    this.icon = icon;
    this.images = [];
  }
}
