export class Size {
  width: number;
  height: number;
  isEqual = (size: Size): boolean =>
    this.width === size.width && this.height === size.height;
  setBy = (size: Size): void => {
    this.width = size.width;
    this.height = size.height;
  };
  constructor(width: number = 0, height: number = 0) {
    this.width = width;
    this.height = height;
  }
}

export class GridSize {
  columns: number;
  rows: number;
  isEqual = (gridSize: GridSize): boolean =>
    this.columns === gridSize.columns && this.rows === gridSize.rows;
  setBy = (gridSize: GridSize): void => {
    this.rows = gridSize.rows;
    this.columns = gridSize.columns;
  };
  getCount = (): number => this.columns * this.rows;
  constructor(columns: number = 0, rows: number = 0) {
    this.columns = columns;
    this.rows = rows;
  }
}
