import styled from "styled-components";
import { Card } from "antd";

export const Collection = styled(Card)`
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: -1 4px 9px 0px rgba(240, 242, 244, 0.577);
  border: 1px solid rgb(198, 198, 198);

  & img {
    object-fit: cover;
  }

  & .ant-card-body {
    padding: 0px;
  }
  
  & .ant-card-meta {
    margin-top: 2px;
  }
  
  & .ant-card-meta-avatar {
    padding-right: 4px;
  }
  
  & .ant-avatar {
    width: 40px;
    height: 40px;
    opacity: 0.5;
    transition: opacity 0.3s linear
  }
  
  // & .ant-tabs-tab-active .ant-avatar {
  //   opacity: 1;
  // }
  
  & .ant-card-meta-title {
    height: 40px;
    display: table-cell;
    vertical-align: middle;
  
    opacity: 0.5;
    transition: opacity 0.3s linear
    font-family: Graphik-Regular; // Graphik;
    font-size: 12px;
    color: #222121;
  }
  
  // & .ant-tabs-tab-active .ant-card-meta-title {
  //   font-weight: 600;
  //   opacity: 1;
  // }

`;
