import { observable, computed } from "mobx";
import { CatalogItem } from "app/api";

export default class ImageModel {
  index: number;
  @observable url: string;
  @observable id: string;
  @observable checked: boolean;

  @observable inBasket: number = 0;

  @observable item: CatalogItem;
  @computed get discount(): number {
    const prices = this.prices.map(v => Number(v));
    return prices[0] === prices[1] || !prices[1]
      ? 0
      : Math.round(100 * (prices[0] / prices[1] - 1));
  }
  @computed get prices(): string[] {
    return [
      Number(this.item.instance.sale_price.split(" ")[0]).toFixed(2),
      Number(this.item.variant.instance.price.split(" ")[0]).toFixed(2)
    ];
  }

  constructor(index: number, id?: string, url?: string, item?: CatalogItem) {
    this.index = index;
    this.id = id;
    this.url = url;
    this.item = item;
  }
}
