import styled from "styled-components";
import { Tabs, Button } from "antd";
import { TabsProps } from "antd/lib/tabs";

export const Svg = styled.svg`
  width: 16px;
`;

export const BadgeWrapper = styled.div`
  position: relative;
  & div.badgeWrapper {
    position: absolute;
    right: 0px;
    top: -10px;
  }
`;

export const MyTabs = styled(Tabs)<TabsProps>`
  height: 100%;
  width: calc(100% - 63px - 100px - 40px);
  margin-top: 1px;
  align-items: start;

  & .ant-tabs-nav-scroll {
    display: flex;
  }

  & .ant-tabs.card {
    margin-top: 2px;
  }
  &.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-nav-container {
    height: 100%;
  }

  & .ant-tabs-nav {
    height: 100%;
  }

  & .ant-tabs-nav > div {
    height: 100%;
  }

  & .ant-tabs-nav-container {
    height: 100% !important;
  }

  & .ant-tabs-tab {
    height: 100% !important;
    width: 63px !important;
    margin: 0px !important;
    padding: 0px !important;

    border: 1px solid transparent !important;
  }

  & .ant-tabs-tab:hover {
    border: 1px solid rgba(0, 0, 0, 0.25) !important;
  }

  & .ant-tabs-bar {
    height: 100%;
  }

  & .ant-tabs-nav-wrap {
    height: 100%;
  }

  & .ant-tabs-nav-scroll {
    height: 100%;
  }
`;

export const Container = styled.div`
  padding-top: 5px;

  height: 63px;
  background-color: #ffffff;
  box-shadow: inset 0px 2px 20px -10px rgba(0, 0, 0, 0.5);

  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;

  display: flex;
  flex-direction: row;
  // justify-content: start;
  justify-content: space-between;
  align-items: center;

  flex: 0 0 63px;
`;
export const Icon = styled.div`
  background: lightgray;
  height: 65px;
  margin-top: -3px;

  // height: 100%;
  width: 63px;
  flex: 0 0 auto;

  display: flex;

  & > svg {
    margin: auto;
  }
`;

export const Img = styled.img`
  height: 50px;
  width: 50px;

  margin: auto;
`;

export const ResetBtn = styled(Button)`
  justify-self: end;
  margin-right: 20px;
  border-radius: 25px;
  width: 52px;
  height: 50px;
`;

export const SaveBtn = styled(Button)`
  justify-self: end;
  margin-right: 10px;
  height: 34px;
`;

export const NextOval = styled.div`
  height: 22px;
  width: 22px;
  background-color: #ffffff;
  border: 1px solid rgba(151, 151, 151, 0.145);

  margin-right: 24px;
`;

export const PrevOval = styled.div`
  height: 22px;
  width: 22px;
  background-color: #ffffff;
  border: 1px solid rgba(151, 151, 151, 0.145);

  margin-right: 8px;
  margin-left: 17px;
  opacity: 0.3;
`;
